@-webkit-keyframes effectOne {
  0% {
    -webkit-opacity: 0.3;
    -webkit-transform: scale(0.5);
    background-color: rgba(240, 96, 96, 0.1);
  }
  40% {
    -webkit-opacity: 0.5;
    -webkit-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #f06060, 0 0 0 10px rgba(255, 255, 255, 0.5);
  }
  100% {
    -webkit-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #f06060, 0 0 0 10px rgba(255, 255, 255, 0.5);
    -webkit-transform: scale(1.5);
    -webkit-opacity: 0;
  }
}
@keyframes effectOne {
  0% {
    opacity: 0.3;
    transform: scale(0.5);
    background-color: rgba(240, 96, 96, 0.1);
  }
  40% {
    opacity: 0.5;
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #f06060, 0 0 0 10px rgba(255, 255, 255, 0.5);
  }
  100% {
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #f06060, 0 0 0 10px rgba(255, 255, 255, 0.5);
    transform: scale(1.5);
    opacity: 0;
  }
}
@-webkit-keyframes close-btn {
  0% {
    -webkit-transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(90deg);
  }
}
@keyframes close-btn {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(90deg);
  }
}
.fade {
  opacity: 0;
  transition-property: opacity;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  transition-delay: 0;
}
.fade.in {
  opacity: 1;
}
